import React, {useState} from 'react';
import {InView} from "react-intersection-observer";
import {Col, Row} from "antd";
import '../styles/about.css';
import { Zoom, Fade, Bounce } from 'react-reveal';
import Button from "../components/Button";
import {FolderOpenOutlined, FolderOpenTwoTone} from "@ant-design/icons";

function About() {
    const [show, setShow] = useState(false);

    return (
        <InView onChange={(inView) => setShow(inView)}>
            <div className={'about-section'} id={'about'}>
                <Row style={{width:'100%',height:'100%'}}>
                    <Col xs={12} sm={8}>
                        {
                            show && (
                                <div className={'about-shape-container'}>
                                    <Zoom duration={1000}><span /></Zoom>
                                    <Zoom duration={1000} delay={300}><span /></Zoom>
                                </div>
                            )
                        }
                    </Col>

                    <Col sm={16} style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center'}}>
                        {
                            show && (
                                <Fade delay={700}>
                                    <div className={'about-description'}>
                                    I am a creative and self-motivated Senior Frontend Web and Smart Contract Developer with 10+ years of experience developing useful, approachable user interfaces. <i style={{color:'red'}}>All You Need is A!</i>
                                    </div>
                                </Fade>
                            )
                        }

                    </Col>
                </Row>
            </div>
        </InView>
    )
}

export default About;
